body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  box-sizing: border-box;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.Header {
    display: flex;
}


@media only screen and (max-width: 768px) {
    .Header {
        display: unset;
    }
  }

.FullName {
    padding: 4.8rem 3.5rem 3rem 3.5rem;
    flex-basis: 50%;
    text-align: center;
    border-right: 1px solid #939497;
}
.Logo {
    margin: auto;
    width: 13rem;
    height: 13rem;
    border-radius: 220px;
    background-image: url(/static/media/avatar.90a9536e.png);
    background-repeat: no-repeat;
    background-size: cover;
}
.name, .position {
    color: #ffffff;
}
.name, .surname {
    font-size: 1.8rem;
}
.up_size {
    text-transform: uppercase;
}
.surname {
    color: #ba7532;
}
.git_link{
    color: #528ebe;
}
.git_link:hover{
    color: #ffffff;
}

@media  (max-width: 768px) {
    .FullName {
        padding: 4.8rem 3.5rem 0rem 3.5rem;
    }
  }
.section_exp, .section_project {
    font-size: 1.5rem;
    text-transform: uppercase;
    color: #434244;   
}
.section_project {
    text-align: center;
}

@media (max-width: 375px){
    .section_project {
        color: #ffffff;   
    }
}
.AboutMe {
    padding: 6.8rem 5rem 5.5rem 2.8rem;
    flex-basis: 50%;
}

.TopAboutMe::after {
    content: '';
    border-bottom: 1px solid #939497;
    height: 100%;
    display: block;
}

.name_about_me {
    font-size: 1.5rem;
    text-transform: uppercase;
}

.wrap_contact, .text_about_me {
    font-size: 1.1rem;
}
.color_text, .name_about_me {
    color: #a1a1a0;
}
.color_info, .text_about_me {
    color: #ffffff;
}
.color_active {
    color: #528ebe;
}
.color_active:hover {
    color: #ffffff;
}


@media only screen and (max-width: 768px) {
    .AboutMe {
        padding: 0.8rem 5rem 0.5rem 2.8rem;
    }
  }


.nav {
    display: flex;
    justify-content: space-around;
    font-size: 1rem;
    color: #ffffff;
    text-transform: uppercase;
}
.nav_item {
    padding: 1rem 1rem;
}
.nav_link {
    color: #fbac03;
}
a {
    text-decoration: none;
    color: #ffffff;
}

@media only screen and (max-width: 768px) {
    .nav, .Line {
        display: none;
    }
  }
.Experience {
    flex-basis: 70%;
    padding: 10px;
    background-color: #ffffff;
}
.wrap_colum_exp {
    display: flex;
    flex-direction: row-reverse;
    font-size: 1.1rem;
}
.left_exp {
    flex-basis: 30%
}
.right_exp {
    flex-basis: 70%;
    text-align: left;
}

.start_work, .developer_position {
    text-align: left;
}
.developer_position {
    color: #528ebe;
}
.company_name {
    color: #606163;
}
.start_work, .description {
    color: #808183;
}
.description {
    margin-right: 1.5rem;
    margin-bottom: 1rem;
}
.expirience_fix {
    flex-direction: unset;
}
.tetss {
    display: flex;
    justify-content: space-between;
}
@media only screen and (max-width: 910px) {
.right_exp {
    padding-left: 2rem;
  }
}
.Skills {
    flex-basis: 30%;
    background-color: lightgrey;
    padding: 10px;
}
.skillbar {
    border-radius: 15rem;
}
.skillbar-title {
    background-color: #fbac03;
    color: #ffffff;
}
.skillbar-percent {
    color: #222222;
}
.skillbar-bar {
    background-color: #fbac03 !important;
}
.Education, .Education_section {
    background-color: #ffffff;
    padding: 10px;
}
.section_exp {
    margin-top: 0;
    text-align: center;
}
.developer_position {
    text-transform: uppercase;
}
.company_name_blue {
    color: #528ebe;
    text-transform: uppercase;
}
.demo_link {
    color: #528ebe;
}
.demo_link:hover {
    color: #808183;
}
.company_name_blue {
    color: #528ebe;
}

.Hobbies {
    flex-basis: 70%;
    background-color: #ffffff;
    padding: 10px;
}
.wrap_hobbies {
    display: flex;
    justify-content: space-between;
    padding: 3rem;
}
.item_hobbies {
    width: 3rem;
    height: 3rem;
}

@media  (max-width: 768px) {
    .wrap_hobbies {
        padding: 1rem;
    }
  }
.Language {
    flex-basis: 30%;
    background-color: #efeeee;
    padding: 10px;
}

.bar-contain {
    text-align: left;
    border-radius: 15rem !important;
    margin-bottom: 1rem !important;
}

.projects_text {
    position: absolute;
    width: 100%;
}
.Projects::after{
    content: '';
    border-bottom: 1px solid lightgrey;
    display: block;
}


@media (max-width: 375px){
    .projects_text {
        position: static;
        color: #ffffff;
    }
}
.Content {
    width: 100%;
    text-align: center;
    margin-top: 0.3rem;
}
.Line {
    width: 85%;
    margin: auto;
    background-color: #939497;
    height: 1px;
    margin-top: 1rem;
}
.Hobbies_Language {
    border-top: 1px solid lightgrey;
}
.Experience_skills, .Hobbies_Language {
    display: flex;
}

@media only screen and (max-width: 768px) {
    .Experience_skills, .Hobbies_Language {
        display: unset;
    }
    .Experience_skills {
        display: flex;
        flex-direction: column-reverse;
    }
  }
.Experience_skills {
    border-bottom: 1px solid lightgrey;
}
.App {
  max-width: 1440px;
  /* background-color: #ffffff; */
  /* height: 1000px; */
  min-width: -webkit-fit-content;
  min-width: -moz-fit-content;
  min-width: fit-content;
  margin: auto;
  background-color: #222222;
}
li {
  list-style: none;
}
ul{
  padding: 0;
}
.skillbar-percent{
  display: none;
}
.skillbar {
  background-color: #fbac03;
}
